const DEFAULT_PROPS = {
    texture: PIXI.Texture.EMPTY,
    anchor: 0.5,
};

export class Sprite extends PIXI.Sprite {

    constructor(props = {}) {

        // Texture or string = default props with texture override
        if (typeof props === "string" || props instanceof PIXI.Texture) {
            props = {
                ...DEFAULT_PROPS,
                texture: props
            };
        }

        // Assign missing initial default values
        props = {...DEFAULT_PROPS, ...(props || {})};

        // If texture is string - create texture from URL or asset name in cache
        if (typeof props.texture === "string") {
            props.texture = PIXI.Texture.from(props.texture);
        }

        let {texture = DEFAULT_PROPS.texture, ...setupProps} = props;

        super(texture);
        this.setup(setupProps);
    }

    setup(props) {
        let {
            anchor, anchorX = anchor, anchorY = anchor,
            pivot, pivotX = pivot, pivotY = pivot,
            scale, scaleX = scale, scaleY = scale,
            skew, skewX = skew, skewY = skew,
            texture,
            ...plainProps
        } = props;

        if (texture) {
            if (typeof texture === "string") {
                texture = PIXI.Texture.from(texture);
            }
            this.texture = texture;
        }

        Object.assign(this, plainProps);

        if (Number.isFinite(anchorX)) this.anchor.x = anchorX;
        if (Number.isFinite(anchorY)) this.anchor.y = anchorY;
        if (Number.isFinite(pivotX)) this.pivot.x = pivotX;
        if (Number.isFinite(pivotY)) this.pivot.y = pivotY;
        if (Number.isFinite(scaleX)) this.scale.x = scaleX;
        if (Number.isFinite(scaleY)) this.scale.y = scaleY;
        if (Number.isFinite(skewX)) this.skew.x = skewX;
        if (Number.isFinite(skewY)) this.skew.y = skewY;
    }
}
