// Developer shorthands for console usage
import {Core} from "./ported/Base/Core";
import {GameManager} from "./ported/Game/GameManager";
import {BoosterType, UpgradeType} from "./ported/enums";
import * as dat from 'dat.gui';
import {NUtils} from "./ported/Utils/NUtils";

const DEV = {
    reset() {
        Core.Reset();
    },

    popup: {
        nextEpoch() {
            let epochs = Core.GameData.Epochs.epochs.slice(0, 3);
            Core.UI.nextEpochSelection.Show((...args) => {
                console.log("DEBUG: NEXT EPOCH", ...args);
            }, epochs);
        },

        newEpoch(epochId = 1) {
            Core.UI.newEpoch.Show(epochId);
        },

        newCreature(n, epochId = 1) {
            let epoch = Core.GameData.Epochs.epochById.get(epochId);
            if (!epoch) return;
            n = Math.max(0, Math.min(epoch.creatures.length - 1, ~~n));
            Core.UI.newCreature.Show(epoch.creatures[n]);
        },

        moneyBox() {
            Core.UI.moneyBox.Show();
        },

        coinsBooster() {
            Core.UI.coinsBooster.Show();
        },

        settings() {
            Core.UI.settings.Show();
        },

        reset() {
            Core.UI.reset.Show();
        }
    },

    translation: {
        search(strOrRegex, returnAllLangs = false) {
            let res = {};
            if (strOrRegex)
                for (let [key, langs] of Object.entries(Core.Localizator.translations)) {
                    for (let [lang, str] of Object.entries(langs)) {
                        if (str.match(strOrRegex)) {
                            if (returnAllLangs) {
                                res[key] = langs;
                            } else {
                                res[key] = res[key] || {};
                                res[key][lang] = str;
                            }
                        }
                    }
                }
            return res;
        },

        key(strOrRegex) {
            let res = {};
            if (strOrRegex)
                for (let [key, langs] of Object.entries(Core.Localizator.translations)) {
                    if (key.match(strOrRegex)) res[key] = langs;
                }
            return res;
        },
    },

    upgrade: {

        _apply(type, level) {
            let upgrade = Core.GameData.Upgrades.UpgradeByType(type);
            if (!upgrade) return;
            if (level === undefined) {
                level = Core.Player.PlanetsSave.GetUpgradeProgress(type).upgradeIndex + 1;
            }
            level = Math.max(0, Math.min(upgrade.steps.length - 1, ~~level));
            Core.Player.PlanetsSave.Upgrade(type, level);
        },

        bubbleTime(level) {
            this._apply(UpgradeType.BubbleTime, level);
        },

        moneyBox(level) {
            this._apply(UpgradeType.MoneyBox, level);
        },

        bubbleCreature(level) {
            this._apply(UpgradeType.BubbleCreature, level);
        },
    },

    cheats: {
        merge() {
            for (let e of GameManager.gameView.allEntities) {
                if (e.tryMerge) {
                    e.tryMerge();
                } else {
                    e.mouseDown(e.position);
                }
            }
        },

        spawn(creatureId) {
            let {CurrentPlanet} = Core.Player.PlanetsSave;
            if (!CurrentPlanet) return;
            if (!creatureId) {
                let epoch = Core.GameData.Epochs.epochById.get(GameManager.gameView.selectedEpochId);
                let creatures = [...CurrentPlanet.collection];
                if (epoch) {
                    creatures = creatures.filter(id => epoch.creatures.includes(id));
                }
                creatureId = [...CurrentPlanet.collection].filter(id => !epoch || epoch.creatures.includes(id)).pop();
                console.log(creatureId, creatures);
            } else {
                console.log(creatureId);
            }
            if (!creatureId) return;

            if (!CurrentPlanet.collection.includes(creatureId)) {
                return console.log(`SORRY, creature ${creatureId} is not in collection:`, CurrentPlanet.collection.join(","));
            }
            GameManager.gameController.AddCreatureRandomPosition(creatureId, false, false);
        },

        skipEpoch(id = GameManager.gameView.selectedEpochId) {
            let epoch = Core.GameData.Epochs.GetById(id);
            if (!epoch) return;
            let creatures = [...epoch.creatures];
            for (let creatureId of creatures) {
                if (!Core.Player.PlanetsSave.CurrentPlanet.collection.includes(creatureId)) {
                    Core.Player.PlanetsSave.CurrentPlanet.collection.push(creatureId);
                }
            }
            if (!Core.Player.PlanetsSave.CurrentPlanet.epochs.includes(epoch.id)) {
                Core.Player.PlanetsSave.CurrentPlanet.epochs.push(epoch.id);
            }
            let lastCreature = creatures[creatures.length - 1];
            for (let i = 0; i < Math.min(2, creatures.length); i++) {
                GameManager.gameController.AddCreatureRandomPosition(lastCreature, false, false);
            }
        }
    },

    tapChallenge() {
        Core.Player.PlanetsSave.CurrentPlanet.mergeProgress = 0;
        Core.Player.CallOnMergeBonus();
        Core.Player.CallOnChanged();
    },

    enableAdsEvolve() {
        Core.UI.adsEvolve.nextEvolveBonusAvailable = Date.now();
    },

    chest() {
         try {
            Core.UI.game.chestBtn.Go();
        } catch (e) {
            console.warn("chestBtn disabled");
            console.error(e);
        }
    },
    
    _createGUI() {

        let s = document.body.appendChild(document.createElement("style"));
        s.innerHTML = `
            .cr.function .property-name {width: 100%;}
        `;

        const gui = new dat.GUI({
            name: 'Test cheats',
            useLocalStorage: false
        });
        this.GUI = gui;

        guiSettings(gui);
        guiBasicControls(gui);
        guiUpgrades(gui);
        guiBoosters(gui);
        guiMissionTimer(gui);
        guiEpochs(gui);
        guiTestPopups(gui);
    }
};
window.DEV = DEV;

const p = new URLSearchParams(window.location.search);
if (p.has("TEST_GUI")) {
    let guiInterval = setInterval(() => {
        if (!Core.Player || !Core.Player.Initialized) return;
        clearInterval(guiInterval);
        DEV._createGUI();
    }, 500);
}

function guiBasicControls(parent) {
    parent.add(Core.Player, "MoneySoftStr").name("Gold").listen();
    parent.add(GameManager.gameController, "MoneySoftSpeedStr").name("Gold/sec").listen();
    parent.add(Core.Player, "SpinsNumber").name("Spins").min(0).max(Core.Player.MaxSpinsNumber).step(1).listen();
    parent.add({value: ""}, "value").name("Add money").onFinishChange(function (value) {
        Core.Player.MoneySoft += BigInt(value);
        if (Core.Player.MoneySoft < 0n) Core.Player.MoneySoft = 0n;
        this.setValue("");
    });
    parent.add({value: ""}, "value").name("Add diamonds").onFinishChange(function (value) {
        Core.Player.MoneyHard += BigInt(value);
        if (Core.Player.MoneyHard < 0n) Core.Player.MoneyHard = 0n;
        this.setValue("");
    });

    parent.add({value: () => DEV.cheats.spawn()}, "value").name("Spawn top creature in epoch");
    parent.add({value: () => DEV.cheats.skipEpoch()}, "value").name("Skip current epoch");
    parent.add({value: () => DEV.chest()}, "value").name("Run chest");
    parent.add({value: () => DEV.tapChallenge()}, "value").name("Run tap challenge");
    parent.add({value: () => DEV.enableAdsEvolve()}, "value").name("Skip ads evolve timeout");
}

function guiSettings(parent) {
    let folder = parent.addFolder(`Common settings`);
    folder.add(Core.Localizator, "locale", Core.Localizator.locales).name("Language").onFinishChange(function (value) {
        window.location.reload();
    });
    folder.add(Core.Store, "VipActive").name("VIP").listen();
    folder.add(Core.Player, "NoAds").name("No ads").listen();
    folder.add(Core.LocalSettings, "Sound").name("Sound").listen();
    folder.add(Core.LocalSettings, "Music").name("Music").listen();
    folder.add({value: () => Core.Reset()}, "value").name("RESET DATA");
}

function guiPlanetUpgrades(parent, planet) {
    let planetData = Core.GameData.Planets.GetPlanet(planet.planetId);
    let planetName = Core.Localizator.Get(planetData.planetName);
    let folder = parent.addFolder(`${planet.planetId}. ${planetName} upgrades`);
    let upgrades = [UpgradeType.BubbleTime, UpgradeType.MoneyBox, UpgradeType.BubbleCreature];

    for (let type of upgrades) {
        let upgrade = Core.GameData.Upgrades.UpgradeByType(type);
        if (upgrade) {
            let progress = planet.GetUpgradeProgress(type);
            let maxLevel = upgrade.steps.length - 1;
            folder.add(progress, "upgradeIndex", 0, maxLevel, 1)
                .name(UpgradeType[type])
                .listen()
            ;
        }
    }
}

function guiUpgrades(parent) {
    let planetIds = new Set();
    let folder = parent.addFolder("Upgrades");
    for (let planet of Core.Player.PlanetsSave.planets) {
        guiPlanetUpgrades(folder, planet);
        planetIds.add(planet.planetId);
    }
    Core.Player.on("CurrentPlanetChanged", () => {
        let planet = Core.Player.PlanetsSave.CurrentPlanet;
        if (!planetIds.has(planet.planetId)) {
            guiPlanetUpgrades(folder, planet);
            planetIds.add(planet.planetId);
        }
    });
}

function guiBoosters(parent) {
    let folder = parent.addFolder("Boosters");
    for (let type of [BoosterType.BubbleTime, BoosterType.CreaturesDiscount, BoosterType.Income, BoosterType.IncomeX4]) {
        guiBoosterTimer(folder, type);
    }
}

function guiBoosterTimer(parent, type) {
    let name = BoosterType[type];
    let folder = parent.addFolder(name);
    let timerObj = {tSeconds: 0};
    Core.Player.on("Changed", () => {
        let timer = Core.Player.Timers.GetBoosterTimer(type);
        timerObj.tSeconds = timer && !timer.IsFinished ? timer.tSeconds : 0;

        folder.name = name + (timerObj.tSeconds
                ? ` (${NUtils.FormatSecondsToTime(timerObj.tSeconds)})`
                : ""
        );
    });

    folder.add(timerObj, "tSeconds").name("Seconds").listen();
    folder.add({value: 0}, "value").name("Change by").onFinishChange(function (value) {
        Core.Boosters.AddBoosterTime(type, ~~value);
        this.setValue(0);
    });
}

function guiMissionTimer(parent) {
    let name = "Mission";

    let folder = parent.addFolder(name);
    let timerObj = {tSeconds: 0};
    Core.Player.on("Changed", () => {
        let activeMission = Core.Player.Missions.missionInProgress;
        if (!activeMission) {
            folder.hide();
            return;
        }
        let timer = Core.Player.Timers.GetTimerById(activeMission.timerId);
        timerObj.tSeconds = timer && !timer.IsFinished ? timer.SecondsLeft : 0;
        folder.name = name + (timerObj.tSeconds
                ? ` (${NUtils.FormatSecondsToTime(timerObj.tSeconds)})`
                : ""
        );
        folder.show();
    });

    folder.add(timerObj, "tSeconds").name("Seconds").listen();
    folder.add({value: 0}, "value").name("Change by").onFinishChange(function (value) {
        let activeMission = Core.Player.Missions.missionInProgress;
        if (activeMission) {
            let timer = Core.Player.Timers.GetTimerById(activeMission.timerId);
            if (timer && !timer.IsFinished) {
                timer.MinusSeconds(~~value * -1);
            }
        }
        this.setValue(0);
    });
}

function guiEpochs(parent) {
    let folder = parent.addFolder("Epochs");
    for (let epoch of Core.GameData.Epochs.epochs) {
        let epochFolder = folder.addFolder(epoch.id + ". " + Core.Localizator.Get(epoch.epochName));
        let creaturesMap = {};
        for (let creatureId of epoch.creatures) {
            let c = Core.GameData.Creatures.creaturesDic.get(creatureId);
            let prop = `${c.id}. ${Core.Localizator.Get(c.creatureName)}`;
            creaturesMap[prop] = c.id;
        }

        let creaturesSelector = epochFolder.add({value: ""}, "value", creaturesMap)
            .name("Creatures")
            .onChange((...args) => console.log(...args))
        ;
        epochFolder.add({
            value: () => {
                let creatureId = ~~creaturesSelector.getValue();
                if (!Core.Player.PlanetsSave.CurrentPlanet.collection.includes(creatureId)) {
                    creaturesSelector.setValue(0);
                    return console.log(`TEST: creature ${creatureId} is not opened yet`);
                }
                GameManager.gameController.AddCreatureRandomPosition(creatureId, false, false);
            }
        }, "value").name("Spawn selected creature");
    }
}

