import {CANVAS_ID} from "@dp-instant/core";
import {Preloader} from "./Preloader";
import {ResourceLoader} from "./ResourceLoader";
import {Core} from "./ported/Base/Core";

import {Time} from "./ported/Utils/Unity/Time";
import {GameManager} from "./ported/Game/GameManager";

import "./cheats";

export class Application extends PIXI.Application {
    static instance;

    static create(opts) {
        this.instance = new this(opts);
        return this.instance;
    }

    _resizeTimer;
    paused = false;

    windowRect = new PIXI.Rectangle(0, 0, window.innerWidth, window.innerHeight);
    viewport = new PIXI.Rectangle(0, 0, window.innerWidth, window.innerHeight);

    constructor(opts = {}) {
        super({
            autoStart: true,
            antialias: true,
            autoDensity: true,
            // resolution: window.devicePixelRatio,
            resolution: 2,
            backgroundColor: 0x000000,
            backgroundAlpha: 0.1,
            view: document.querySelector(`#${CANVAS_ID}`),
            ...opts
        });

        this.resizeLayout = this.resizeLayout.bind(this);
        this.fitLayout = this.fitLayout.bind(this);
        this.tick = this.tick.bind(this);

        this.events = new PIXI.utils.EventEmitter();

        this._resizeTimer = setInterval(() => {
            let changed =
                this.windowRect.width !== window.innerWidth ||
                this.windowRect.height !== window.innerHeight;
            if (!changed) return;
            this.windowRect.width = window.innerWidth;
            this.windowRect.height = window.innerHeight;
            this.events.emit("viewport-resize", this.windowRect);
        }, 500);

        this.events.on("viewport-resize", this.resizeLayout);
        this.events.on("resize", this.fitLayout);

        this.wrapper = this.stage.addChild(new PIXI.Container());

        this.preloader = this.wrapper.addChild(new Preloader(this.loader));

        this.resizeLayout();
    }

    destroy(...args) {
        this._resizeTimer = clearInterval(this._resizeTimer);
        return super.destroy(...args);
    }

    resizeLayout(windowRect = this.windowRect) {
        // Portrait only. All wider ratios should be centered;
        const MAX_RATIO = 0.75;

        let {width, height} = windowRect;

        let viewport = new PIXI.Rectangle(0, 0, Math.min(width, height * MAX_RATIO), height);
        if (viewport.width < width) {
            viewport.x = (width - viewport.width) * 0.5;
        }

        let wrapper = this.view.parentNode;
        if (wrapper) {
            Object.assign(wrapper.style, {
                left: viewport.x + "px",
                top: viewport.y + "px",
                width: viewport.width + "px",
                height: viewport.height + "px"
            });
        }

        this.renderer.resize(viewport.width, viewport.height);
        this.viewport.width = this.renderer.width;
        this.viewport.height = this.renderer.height;

        this.wrapper.scale.set(1 / this.renderer.resolution);

        this.events.emit("resize", this.viewport);
    }

    fitLayout(viewport = this.viewport) {
        let {width, height} = viewport;
        if (this.preloader) {
            this.preloader.fitLayout(viewport);
        }
        if (this.game) {
            this.game.fitLayout(viewport);
        }
        if (this.ui) {
            this.ui.fitLayout(viewport);
        }
    }

    async load() {
        return new Promise(resolve => {
            ResourceLoader.initLoader(this.loader);
            this.preloader.visible = true;
            this.preloader.once("complete", resolve);
            this.loader.load();
        });
    }

    async run() {
        await Core.Init(this);
        await GameManager.Init();

        this.preloader.visible = false;
        this.ticker.add(this.tick);

        this.game = this.wrapper.addChild(GameManager.gameView);
        this.ui = this.wrapper.addChild(Core.UI);
        this.vfx = Core.UI.vfxLayer;
        this.fitLayout();

        await Core.onGameSceneLoaded();
        await Core.Authenticate();
        // this.___test();
    }

    tick(delta) {
        if (this.paused) return;
        let s = this.ticker.elapsedMS / 1000;
        Time.tick(s);
        Core.tick(s);
        GameManager.tick(s);
    }

    ___test() {}
}
