import {Sprite} from "./Sprite";

class ProgressBar extends PIXI.Container {
    constructor(props) {
        super();

        this.props = {
            border: 0xcccccc,
            bar: 0x00ff00,
            ...(props || {})
        };
        this.viewport = new PIXI.Rectangle(0, 0, 100, 20);

        this._progress = 0;
        this._drawBorder();
        this._drawBar();
    }

    get progress() {
        return this._progress;
    }

    set progress(val) {
        this._progress = Math.max(0, Math.min(1, val || 0));

        //remove or abstract for all expect facebook
        // FBInstant.setLoadingProgress(this._progress * 100);
        //console.log("progress", this._progress);
        this._drawBar();
    }

    _drawBorder() {
        let {width, height} = this.viewport;
        this.border = this.border || this.addChild(new PIXI.Graphics());
        this.border
            .clear()
            .lineStyle(1, this.props.border)
            .drawRoundedRect(0, 0, width, height, height * 0.5);
    }

    _drawBar() {
        let {width, height} = this.viewport;
        this.bar = this.bar || this.addChild(new PIXI.Graphics());
        this.bar.clear();
        if (this._progress > 0) {
            this.bar
                .beginFill(this.props.bar)
                .drawRoundedRect(0, 0, width * this._progress, height, height * 0.5);
        }
    }

    fitLayout(viewport = this.viewport) {
        this.viewport = viewport.clone();
        this.position.set(this.viewport.x, this.viewport.y);
        this._drawBorder();
        this._drawBar();
    }
}

export class Preloader extends PIXI.Container {
    loader = null;
    back = null;
    logo = null;
    monsters = null;

    constructor(loader = null) {
        super();

        this.fitLayout = this.fitLayout.bind(this);

        this.back = this.addChild(new Sprite(PIXI.Texture.from("assets/preloader/back.png")));

        this.monsters = this.addChild(
            new Sprite(PIXI.Texture.from("assets/preloader/monsters.png"))
        );

        this.logo = this.addChild(new Sprite(PIXI.Texture.from("assets/preloader/logo.png")));

        this.progressBar = this.addChild(new ProgressBar());

        this.setLoader(loader);
    }

    get progress() {
        return this.progressBar.progress;
    }

    set progress(val) {
        this.progressBar.progress = val;
    }

    detachLoader() {
        if (this.loader) {
        }
        this.loader = null;
    }

    setLoader(loader) {
        this.detachLoader();
        this.loader = loader;
        this.loader.onProgress.add((loader, resources) => {
            this.progress = loader.progress / 100;
        });
        this.loader.onComplete.add((loader, resources) => {
            this.emit("complete");
        });
    }

    // This method is needed as preloader has no preloaded assets
    _resizeWhenReady(mc, fn) {
        if (mc.texture.baseTexture.resource.valid) {
            return fn();
        } else {
            mc.texture.baseTexture.once("loaded", fn);
        }
    }

    fitLayout(viewport) {
        let {width, height} = viewport;
        this.position.set(width * 0.5, height * 0.5);

        let aspectRatio = 9 / 16;

        width = Math.min(width, height * aspectRatio);

        let rect = viewport.clone();
        rect.width = width;

        let {back, monsters, logo} = this;

        this._resizeWhenReady(back, () => {
            back.scale.set(
                Math.max(viewport.width / back.texture.width, viewport.height / back.texture.height)
            );
        });

        this._resizeWhenReady(monsters, () => {
            monsters.scale.set((rect.height * 0.8) / monsters.texture.height);
        });

        this._resizeWhenReady(logo, () => {
            logo.scale.set((rect.width / logo.texture.width) * 0.75);
            logo.y = -rect.height * 0.45 + logo.height * 0.5;
        });

        let progressBarRect = rect.clone();
        progressBarRect.width *= 0.75;
        progressBarRect.height = progressBarRect.width * 0.03;
        progressBarRect.x = -progressBarRect.width * 0.5;
        progressBarRect.y = height * 0.4;
        this.progressBar.fitLayout(progressBarRect);
    }
}
